#sidebar {
    * {
        transition: .5s;
    }
    #header {
        background-color: lighten(black, 30%);
        transition: 2s;
        #title {
            position: absolute;
            margin-left: 100px;
        }
        svg {
            margin-top: 6px;
            margin-right: 8px;
            cursor: pointer;
        }
    }
    #children {
        display: flex;
        flex-direction: column;
        min-width: 400px;
    }
    .hidden {
        transform: translate(-400px)

    }
    input, button {
        min-height: 30px !important;
        padding: 3px;
        margin: 10px 3px;
    }
    margin:0px;
    background-color: black;
    color: white;
    font-size: 24px;
    text-align: right;
    width: 40px;
    height: 100%;
    transition: .5s;
    
    
    
    &.expanded {

        width: 400px;
        
    }
}