#search, #playlist {

    svg {
        margin-left: 15px;
        margin-bottom: 4px;
        vertical-align: text-bottom;
        flex-shrink: 0;
        font-size: 25px;
        transition: .1s;
        &.button {
            cursor: pointer;
            &:hover {
                color: red;
            }
        
        }
       
    }

    #heading {
        margin-top: 30px;
    }
    #alt {
        font-size: 15px;
        margin: 10px;
    }
    text-align: center;
    #search_bar {
        margin: 10px 20px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        input, button {
            flex-grow: 1;
            font-size: 15px;
            min-height: 20px !important;
            padding: 2px;
            margin: 5px 0px;
            border: 0px;
        }
        input {
            padding: 2px 8px;
        }
        button {
            margin-left: 10px;
        }
    }
    #search_results {
        &::-webkit-scrollbar-thumb {
            background-color: red;
        }
        background-color: white;
        color: black;
        margin: 5px 20px;
        padding: 5px;
        border-radius: 5px;
        max-height: 300px;
        overflow-y: scroll;
        overflow-x: hidden;
        display: flex;
        flex-direction: column;
        min-width: 300px;
        .result {
            padding: 4px;
            border-radius: 5px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 3px;
            background: darken(white, 10%);
            font-size: 16px;
            text-align: left;
            flex-wrap: nowrap;
            .info{
                display: flex;
                img {
                    height: 45px !important;
                    width: 45px !important;
                    margin-left: 15px;
                }
                .text {
                    display: flex;
                    flex-direction: column;
                    margin: 0px 5px;
                }
            }
            
            a {
                &.song {
                    color: red;
                }
                &.artist {
                    color: black;
                }
            }
            svg {
                margin-right: 30px;
                flex-shrink: 0;
                font-size: 25px;
                transition: .1s;
                &.button {
                    cursor: pointer;
                    &:hover {
                        color: red;
                    }
                
                }
               
            }
        }
    }
    
}